import React, { useState } from 'react';
import { BrowserRouter } from 'react-router-dom'
import AppRouter from './components/AppRouter';
import { Container } from '@mui/material';
import { appContext } from "./context/index";
import FooterOnPage from './components/UI/FooterOnPage';


const App = () => {
  
  const [theme, setTheme] = useState(true); // true - dark; false - light
  const [preferCity, setPreferCity] = useState(0); // 0 - Moscow; 1 - SPb

  return (
    <appContext.Provider value={{
      theme, setTheme,
      preferCity, setPreferCity
    }}>
      <BrowserRouter>
        {theme
          ? <style>:root {"{--font: 'Montserrat', sans-serif;--fontSecond: 'Indie Flower', cursive;--color: #fff;--colorSecond: #6a6a6aa8;--colorOpacity: #ffffffe0;--colorInvert: #000;--colorWhite: #fff;--colorAction: #1ab13e;--colorActionSecond: #d50100;--helpColor: #c4c3c6; --bgBody: #101010;--bgSecond: #181818;--bgSecondHelper: rgba(0, 0, 0, 0.75);}"}</style>
          : <style>:root {"{--font: 'Montserrat', sans-serif;--fontSecond: 'Indie Flower', cursive;--color: #000;--colorSecond: #c3c3c3;--colorOpacity: #000000e0;--colorInvert: #fff;--colorWhite: #fff;--colorAction: #1ab13e;--colorActionSecond: #d50100;--helpColor: #a1a1a1; --bgBody: #fff;--bgSecond: #f9f9f9;--bgSecondHelper: rgba(0, 0, 0, 0.06);}"}</style>
        }
        <Container maxWidth="lg">
          <AppRouter />
          <FooterOnPage />
        </Container>
      </BrowserRouter>
    </appContext.Provider>
  );
}

export default App;