import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import SwitchLanguagePrefer from "../UI/Switchers/SwitchLanguagePrefer";
import SwitchThemeColor from "./Switchers/SwitchThemeColor";

const HeaderOnPage = () => {

    return (
        <Box
            className="flex-column"
            sx={{ mt: 3 }}
        >
            {/* <KebabDiningIcon fontSize="large"/> */}
            <Box className="flex-row gap-1">
                <SwitchLanguagePrefer />
                <SwitchThemeColor /> 
            </Box>
            <Typography 
                variant="h2" 
                // className="fontSecond"
                sx={{ fontWeight: '800' }}
            >
                Tour De Shaverma
            </Typography>
        </Box>
    )
}

export default HeaderOnPage;