import { Chip, Divider, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useNavigate } from "react-router-dom";

const TopCard = ({title, rating, price, subway, id, info}) => {

    const history = useNavigate()

    return (
        <Box 
            className="flex-row gap-3 wrap eat-card"
            onClick={() => history('404')}
        >
            <Typography 
                variant="h4" 
                sx={{ fontWeight: '700', width: '15%' }}
            >
                {Number(id) === 0
                ?   <>Топ {Number(id) + 1}&nbsp;👑</>
                :   <>Топ {Number(id) + 1}</>
                }
            </Typography>
            <Divider
                className="divider"
                orientation="vertical" 
                flexItem 
            />
            <Box 
                className="flex-row wrap eat-info gap-0"
                sx={{ width: '70%' }}
            >
                <Box className="flex-row wfull wrap">
                    <Typography 
                        variant="h4" 
                        sx={{ fontWeight: '300', mr: 3 }}
                    >
                        {title || undefined} {/* „‟ */}
                    </Typography>
                    <Box className="flex-row gap-0 wrap">
                        {rating > 8
                            ?   <Chip
                                    className="chip-rate"
                                    variant="filled"
                                    color="success"
                                    label={`⭐ ${rating || NaN}`}
                                    sx={{ fontWeight: '300', fontSize: 18 }}
                                />
                            :   <>
                                    {rating < 3
                                        ?   <Chip
                                                className="chip-rate"
                                                variant="filled"
                                                color="error"
                                                label={`⭐ ${rating || NaN}`}
                                                sx={{ fontWeight: '300', fontSize: 18 }}
                                            />
                                        :   <Chip
                                                className="chip-rate"
                                                variant="filled"
                                                color="warning"
                                                label={`⭐ ${rating || NaN}`}
                                                sx={{ fontWeight: '300', fontSize: 18 }}
                                            />
                                    }
                                </>
                        }
                        <Chip
                            className="chip-rate"
                            variant="outlined"
                            label={`🏷️ ${price || NaN} ₽`}
                            sx={{ fontWeight: '300', fontSize: 18 }}
                        />
                        <Chip
                            className="chip-rate"
                            variant="filled"
                            color="info"
                            label={`📍 ${subway || undefined}`}
                            sx={{ fontWeight: '300', fontSize: 18 }}
                        />
                    </Box>
                    {/* ⭐&nbsp;{rating || NaN}/10
                    </Chip>  */}
                </Box>
                <Box className="flex-row gap-1 wrap">
                    {/* <Typography 
                        variant="subtitle" 
                        className="note"
                        sx={{ fontWeight: '300' }}
                    >
                        🏷️&nbsp;{price || NaN}₽ 
                    </Typography> 
                    <Typography 
                        variant="subtitle" 
                        className="note"
                        sx={{ fontWeight: '300' }}
                    >
                        🏷️&nbsp;{price || NaN}₽ 
                    </Typography> 
                    <Typography 
                        variant="subtitle" 
                        className="note"
                        sx={{ fontWeight: '300' }}
                    >
                        🚇&nbsp;м.&nbsp;{subway || undefined}
                    </Typography>  */}
                    <Typography 
                        variant="subtitle" 
                        className="note"
                        sx={{ fontWeight: '300' }}
                    >
                        {info || 'Дополнительная информация отсутствует'}
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

export default TopCard;